<template>
    <div class="HomePage">
        <div class="header">
            <p>中国石油大学二手市场物品交易社区</p>
        </div>
        <div class="search box-center">
            <label>
                <input type="text"/>
            </label>
            <div class="placeholder absolute-center">
                <p>搜索宝贝</p>
            </div>
        </div>
    </div>
</template>

<script>
    const HomePage = {
        name: 'HomePage',
        data () {
            return {}
        }
    };
    export default HomePage
</script>


<style>
    .HomePage {
    }
    .HomePage .header {
        background-color: #189FD9;
        text-align: center;
        font-size: 2rem;
        height: 40px;
        line-height: 40px;
    }
    .HomePage .search {
        height: 20px;
        width: 95%;
        color: #999898;
        position: relative;
        margin-bottom: 5px;
        margin-top: 5px;
    }
    .HomePage .search input {
        height: 100%;
        width: 100%;
        background-color: #E3E3E3;
        border: none;
        outline-style: none;
    }
    .HomePage .search .placeholder {
        position: absolute;
        top: 0;
    }
</style>



